<template>
<main >
        <div  style="display: none">
            {{ss}}
        </div>  

        <div  ref="element">

        </div>
		<div  id="form_container">
			<div  class="row">
				<div  class="col-lg-5">
					<div  id="left_form" style="text-align: center">
