					</div>
				</div>


				<div  class="col-lg-7">

					<div  id="wizard_container">
						<div  id="top-wizard">
							<div  id="progressbar"></div>
						</div>
						
						<div >
							<input  id="website" name="website" type="text" value=""/>
							
							<div  id="middle-wizard">
                  <div  class="submit step" style="text-align: center">
		              
                  <div id="recordingsList"></div>
                    
                    <h4  style="text-align: justify" class="main_question">
                        <strong  v-if="qs.isViewPaging">{{counter+1}}/{{qs.questions.length}}</strong>
