                    <span  v-if="showRequire" class="error" style="">{{chekText}}</span>
                   
                    <img  v-if="ques.url && ques.isPicture" v-bind:src="ques.url" style="width:90%"></img>
                    <video  v-if="ques.url && !ques.isPicture" width="320" height="240" controls>
                      <source :src="ques.url" type="video/mp4">
                      Your browser does not support the video tag.
                    </video>

                    <div  v-if="ques.kind=='numberScale'"> 
                        <div  v-for="(answ,index1) in ques.answers " :key="answ.id" >
