                                <td  style="text-align: right;    padding-bottom: 10px;padding-top: 10px;">
                                    {{answ.text}}
                                </td>
                            </div>
                        <table  style="width: 100%">
                            <tr >
                            <td  v-bind:width="(100/(getHeaders().length ))+'%'" v-for="(itms,mindex) in getHeaders() " :key="itms.id"  v-bind:style="itms.style">
                            <span >
                                {{itms.text}}

                            </span>
                            </td>

                            </tr>
                        </table> 
                        <table  style="width:100%;text-align:center;">
                               
                                     <tr  style="border-bottom:3pt solid blue;">
                                         
                                             <span  v-bind:width="(100-getMinMax(ques.minItem,ques.maxItem).length*10)+'%'" v-if="shErr[ques.id][answ.id]" class="error" style="position: relative;">اجباری</span>  
                                              
                                             <td  v-for="(itms,mindex) in getMinMax(ques.minItem,ques.maxItem) " :key="itms.id" > 
                                                     
                                                 <div  v-if="myansw[ques.id][answ.id] != itms" class="col-lg-1 col-2" v-on:click="numberClick(ques.id,answ.id,itms)" style="border-width: 2px;width: 30px;border-style: solid;height: 30px;border-color: #5e76ed;cursor: pointer; top: 50%;transform: translate(-50%, 0%);">
                                                      
                                                     
                                                         {{itms}}
                                                      
                                                 
                                                 </div>
         
                                                 <span >
                                                     
                                                 <div  v-if="myansw[ques.id][answ.id] == itms" class="col-lg-1 col-2" v-on:click="numberClick(ques.id,answ.id,itms)" style="border-width: 2px;width: 30px;border-style: solid;height: 30px;border-color: #5e76ed;background: blue;cursor: pointer; top: 50%;transform: translate(-50%, 0%);">
                                                     
                                                     
                                                         {{itms}} 
                                                 
                                                 </div>
                                                 </span>
     
                                             </td> 
                                     </tr> 
                                     
                        </table> 
                        </div>

                    </div>
 
                   
                    <table  v-if="ques.kind!='endpage' && ques.kind!='page' && ques.kind!='numberScale'" 
                            style="text-align: right;" class="col-lg-12">
                        
                        <template  v-for="(answ,index1) in ques.answers "   >
                            
                            <tr >
                                <td  v-if="ques.showNumber">
                                    {{index1+1}}
                                </td>
                                    
                                <td  v-if="answ.url" style="width: 45px;">
                                    <img  v-bind:src="answ.url" style="width: 40px;"></img>
                                </td> 
                                <td  v-if="!ques.place &&  !(ques.kind=='radio'|| ques.kind=='check'|| ques.kind=='priority')">
                                    {{answ.text}}
                                </td>
                                <td  v-if="(ques.kind=='singleLine' || ques.kind=='email' || ques.kind=='link') && !answ.isOther">
                                    <span  v-if="shErr[ques.id][answ.id]" class="error" style="position: relative;">اجباری</span>  
                                    <input  v-if="ques.place" type="text" class="form-control" v-model="myansw[ques.id][answ.id]" v-bind:placeholder="answ.text"/>

                                    <input  v-if="!ques.place" type="text" class="form-control" v-model="myansw[ques.id][answ.id]"/>
                                            
                                </td>
                                
                                <td  v-if="(ques.kind=='int' || ques.kind=='decimal') && !answ.isOther">
                                    <span  v-if="shErr[ques.id][answ.id]" class="error" style="top:-20px">اجباری</span>  
                                    <input  v-if="ques.place" type="number" class="form-control" inputmode="numeric" pattern="[0-9]*" v-on:keyup="keyupp($event)" lang="en-150" v-on:change="replaceNumber(answ.id,$event)" v-model="myansw[ques.id][answ.id]" v-bind:placeholder="answ.text"/>

                                    <input  v-if="!ques.place" v-on:keyup="keyupp($event)" v-on:change="replaceNumber(answ.id,$event)" type="number" inputmode="numeric" pattern="[0-9]*" lang="en-150" class="form-control" v-model="myansw[ques.id][answ.id]"/>
                                    <div  style="float: left;">

                                        {{getSeperated(myansw[ques.id][answ.id])}}       
                                    </div>     

                                </td>
                                <td  v-if="ques.kind=='multiLine' && !answ.isOther">
                                    <span  v-if="shErr[ques.id][answ.id]" class="error" style="top:-20px">اجباری</span>  
                                    <textarea  v-if="ques.place" type="text" class="form-control" v-model="myansw[ques.id][answ.id]" v-bind:placeholder="answ.text"></textarea>

                                    <textarea  v-if="!ques.place" type="text" class="form-control" v-model="myansw[ques.id][answ.id]"></textarea>
                                </td>

                                
                                <td  v-if="ques.kind=='dateTime' && !answ.isOther">
                                    <span  v-if="shErr[ques.id][answ.id]" class="error" style="top:-20px">اجباری</span>  
                                    
                                    <date-picker  v-model="myansw[ques.id][answ.id]" v-bind:clearable="true"></date-picker>
                                </td>
                                <td  v-if="ques.kind=='linearScale' && !answ.isOther">
                                    <span  v-if="shErr[ques.id][answ.id]" class="error" style="top:inherit">اجباری</span>  
                                    <div  style="direction: ltr">  
    
                                            <tool-rating  v-bind:id="answ.id" v-bind:number="ques.pointerNumber" v-bind:shape="getPointer(ques.pointerType)" v-bind:selected="myansw[ques.id][answ.id]" v-on:selectedChange="ratechange"></tool-rating>


                                            
            
                                    </div>
                                </td>
                                <td  v-if="ques.kind=='numberScale' && !answ.isOther">
                                    <span  v-if="shErr[ques.id][answ.id]" class="error" style="top:-20px">اجباری</span>  
                                    <div  class="col-lg-9 col-10">
                                        <table >
                                            <tr  v-if="answ.header">
                                                <td  v-for="(hdr,hdri) in answ.header " :key="hdr.id" >
                                                    {{hdr.text}}
                                                </td>
                                            </tr>
                                            <tr >
                                                <td  v-for="(itms,mindex) in getMinMax(ques.minItem,ques.maxItem) " :key="itms.id" > 
                                                            
                                                    <div  class="col-lg-1 col-2" v-on:click="itmClick(ques.id,answ.id)" style="width: 30px;border-style: solid;height: 30px;border-color: #5e76ed;">
                                                        <span >
                                                        
                                                            {{itms}}
                                                        </span>
                                                    
                                                    </div>
                                                </td> 
                                            </tr>
                                        </table> 
            
                                    </div>
                                </td>
                                <td  v-if="ques.kind=='radio' && !answ.isOther">
                                    <div  class="form-group radio_input">
                                        <label  v-on:click="radioClick(ques.id,answ.id)" style="width: 100%">  
                                            <div  class="row">
                                                    <div  class="col-1 radiouncheck" v-if="!myansw[ques.id][answ.id]"></div>
                                                    <div  class=" col-1 radiocheck" v-if="myansw[ques.id][answ.id]"></div> 
                                                    <span  class=" col-10">
                                                        
                                                    {{answ.text}}
                                                    </span>

                                                </div>    
                                        </label>
                                    </div>
                                </td>
                                <td  v-if="ques.kind=='check' && !answ.isOther">  
                                        <div  class="form-group radio_input">
                                                <label  v-on:click="checkClick(ques.id,answ.id)" style="width: 100%">
                                                <div  class="row">
                                                    <div  class="col-1 checkboxuncheck" v-if="!myansw[ques.id][answ.id]"></div>
                                                    <div  class=" col-1 checkboxcheck" v-if="myansw[ques.id][answ.id]"></div> 
                                                    <span  class=" col-10">
                                                        
                                                    {{answ.text}}
                                                    </span>

                                                </div>   
                                                </label>
                                            </div>
                                </td>
                                <td  v-if="ques.kind=='priority' && !answ.isOther">
                                    <div  class="row">
                                    
                                        <div  class="col-lg-1 col-2" v-on:click="itmClick(ques.id,answ.id)" style="width: 30px;border-style: solid;height: 30px;border-color: #5e76ed;">
                                            <span >
                                            
                                                {{myansw[ques.id][answ.id]}}
                                            </span>
                                        
                                        </div>
                                        <div  class="form-group radio_input col-lg-10 col-9">
                                            <label > 
                                                {{answ.text}}
                                            </label>
                                        </div>
                                        
                                    </div>
                                </td>
                                <td  v-if="answ.isOther"> 
                                        <div  class="form-group radio_input">
                                            <label  style="width: 100%">
                                                <div  class="row"> 
                                                    <div  class=" col-2 ">
                                                        <span  v-if="!ques.place ">
                                                            
                                                    {{answ.text}}
                                                        </span>
                                                    </div> 
                                                    <span  class=" col-10">
                                                        
                                                        <input  v-if="ques.place" type="text" class="form-control" v-model="myansw[ques.id][answ.id]" v-bind:placeholder="answ.text"/>
                                                        <input  v-if="!ques.place" type="text" class="form-control" v-model="myansw[ques.id][answ.id]"/>
                                                    </span>

                                                </div>    
                                            </label>
                                        </div>


    

                                </td>

                            </tr>
                            <tr  v-if="answ.url">
                                <td  colspan="5" style="text-align: center;"> 

                                    <img  v-bind:src="answ.url" style="width: 100%;"></img>
                                </td>
                            </tr>
                            <tr  style="border-bottom:3pt solid blue;" v-if="(ques.kind=='int' || ques.kind=='decimal')||answ.url ">
                                <td  colspan="5"></td>
                            </tr>
                        </template>
                    </table>     


                    <div  style="position: absolute;top:-1000px">
                            <div  class="col-lg-1 checkboxuncheck"></div>
                            <div  class=" col-lg-1 checkboxcheck"></div>  
                            <img  class="radiouncheck"></img>
                            <img  class="radiocheck"></img>
                    </div>

                    

                    
                    
                    
                    
                    

                    
                      <div  style="display: none">

                      {{cc}}
                      </div>

                    
                    <div  class="col-lg-12" style="text-align: center" v-if="ques.kind=='page'">   
                        <div  class="col-lg-12"> 
                            {{ques.description}}
                        </div> 
                        <div  class="col-lg-12 " v-for="(answ,index1) in ques.answers " :key="answ.id" > 
                            <button  type="button" v-on:click="nextPage()" class="forward">{{ques.buttonTitle}}</button>
                        </div>
                    </div>


                    <div  class="col-lg-12" style="text-align: center" v-if="ques.kind=='endpage'">   
                        <div  class="col-lg-12"> 
                            {{ques.description}}
                        </div> 
                        <div  class="col-lg-12 " v-for="(answ,index1) in ques.answers " :key="answ.id" > 
                            <button  v-if="!issend" type="button" v-on:click="submit()" class="forward">{{answ.text}}</button>
                        </div>
                    </div>

                  </div>
								
							</div>
							
							<div >
								<button  type="button" v-on:click="backPage()" v-if="!isEnd && counter!=1 && counter!=0 && ques.canBack" class="backward">قبلی </button>
								<button  type="button" v-on:click="nextPage()" v-if="!isEnd && counter!=0" class="forward">بعدی</button>
								
							</div>
							
            </div>
					</div>
					
				</div>
			</div>
		</div>
	</main>
</template>



<script>
import pages from './components/pages.vue'
export default {
  name:'home',
    components:{pages},  
      data:function()
      {
        return {
          startDate:"",
          trackingCode:"",
          pushtoNext:[],

          showQuestion:{},
          gconds:{},
          qs:{"questions":[]},
          counter:0,
          myansw:{},
          shErr:{},
          ques:{},
          isEnd:false,
          isPublic:false,
          mm:"1397/02/02",
          cc:0,
          showRequire:false,
          chekText:"",
          ss:0,
          mtxt:"",
          thisId:"",
          questionAnsw:{},
          issend:false,
          element:{},
          shape:[0,2,14,17,0,19,10,34,7,50,23,43,38,50,36,34,46,19,31,17],

        }
      },
      
    
    methods: { 
      getElementByRef:function(el){
        return this.$refs[el]
      },
      getMyParent:function(obj){return this.$parent},
      setState:function(obj){
        var al={}
        if(obj)
          for(var a in obj)
          {
            //if(al[a])
            //  continue
            try{
              this[a]=obj[a] 
            }catch(exp){}
          }
          
      },
      getSeperated:function (num)
    {
      if(!num)
        return""
      var c=0
      var ch="";
      for(var a=num.length-1;a>0;a--)
      {
        c++;
        ch=num[a]+ch
        if(c==3)
        {
          c=0
          ch=","+ch
        }
      }
      if(num.length)
        ch= num[0]+ch
      return ch
    },
        ratechange:function (data){
      this.myansw[this.ques.id][data.id]=data.index
      //console.log('>>>>>>',data)
    },
        getPointer:function (name)
    {
      if(name.indexOf("star")>-1)
      {
        return "fa-star";
      }
      if(name.indexOf("thumbs")>-1)
      {
        return "fa-thumbs-up";
      }
      if(name.indexOf("heart")>-1)
      {
        return "fa-heart";
      }
    },
    keyupp:function (event){
        var ew = event.which;
        this.ss++;
        this.showRequire=false 
        if(event.which)
        {
          this.mtxt='is '+event.which
        }
        else
        {
          this.mtxt="noalue"
          this.chekText="لطفا عدد انگلیسی وارد کنید";
        }
        if(ew == 32)
            return true;
        if(48 <= ew && ew <= 57)
            return true;
        // if(65 <= ew && ew <= 90)
        //     return true;
        // if(97 <= ew && ew <= 122)
        //     return true; 
            this.showRequire=true 
          this.chekText="لطفا عدد انگلیسی وارد کنید";
        return false;
    },
    replaceNumber:function (id,eve){
      //console.log('------------------>',eve.target.value)

      var txt=this.myansw[this.ques.id][id];
      var ntext=""
      if(eve.which)
      {
        this.mtxt=eve.which
      }
      else
      {
        this.mtxt="noalue"
      }
      
      for(var  x in txt)
      {
        var a= txt[x];
        if(a=="٠")a="0";
        if(a=="١")a="1";
        if(a=="٢")a="2";
        if(a=="٣")a="3";
        if(a=="٤")a="4";
        if(a=="٥")a="5";
        if(a=="٦")a="6";
        if(a=="٧")a="7";
        if(a=="٨")a="8";
        if(a=="٩")a="9";
        if(a=="۰")a="0";
        if(a=="۱")a="1";
        if(a=="۲")a="2";
        if(a=="۳")a="3";
        if(a=="۴")a="4";
        if(a=="۵")a="5";
        if(a=="۶")a="6";
        if(a=="۷")a="7";
        if(a=="۸")a="8";
        if(a=="۹")a="9";
        ntext+=a;
      }
       
      //console.log('hhhhhhhhhh',ntext)
      this.myansw[this.ques.id][id]=ntext
      this.ss++;
    },
    getAllSubCond:function (cond){
      if(cond.children && cond.children.length)
      {
        this.gconds[cond.id]={questionnaireId:cond.questionnaireId,questionId:cond.questionId,cond:cond,value:{}}
      }
    },
    getAllCond:function (conds){
      var arr=[]
      for(var b in conds)
      {
        var a=conds[b]
        this.getAllSubCond(a.subCond);
      }
    },
    postCond:function (){
      var cnds=[]
      for(var a in this.gconds)
      {
        var b=this.gconds[a]
        cnds.push({questionnaireId:b.questionnaireId,questionId:b.questionId})
      } 
    },
    rebuildCondition:function (){
      for(var a in this.gconds)
      {
        var b=this.gconds[a]
        if(b.questionnaireId==this.thisId)
        {
          if(this.myansw[b.questionId])
          {
             
          }
        }

      }
    },
    getHeaders:function (){
      var arr=[]
      for(var b= this.ques.maxItem;b>=this.ques.minItem-1   ;b--)
      {
        var style='text-align: center;'
        if(this.ques.header['a'+b])
          arr.push({text:this.ques.header['a'+b],style:style})
        
      }
      // for(var a in this.ques.header)
      // {
      //   var style='text-align: center;'
      //   if(this.ques.header[a])
      //     arr.push({text:this.ques.header[a],style:style})
      // }
      arr[0].style='text-align: right;'
      arr[arr.length-1].style='text-align: left;'
      return arr
    },
    getMyQue:function (id,aid){
      for(var a of this.qs.questions)
        //var a=this.qs.questions[jjx]
        if(a.id==id)
        {
          for(var b of a.answers)
          {
            // var b=a.answers[jjj]
            if(b.id==aid)
            {
              return b.text
            }
          }
        }
    },
    isKindeOf:function (id,aid){
      for(var a of this.qs.questions)
        //var a=this.qs.questions[jjx]
        if(a.id==id)
        {
          return a.kind[0]=='singleLine' || 
          a.kind[0]=='multiLine'||
          a.kind[0]=='int' || 
          a.kind[0]=='decimal'|| 
          a.kind[0]=='range'|| 
          a.kind[0]=='numberScale'|| a.kind[0]=='linearScale'
           
        }
    },
    checkCondition:function (cond){
      //console.log('check')
      var val=false
      var tp=cond.type
      if(tp=="and")
      {
        val=true
      }
      for(var jjx in cond.children)
      { 
        var a=cond.children[jjx]
        var v=false
        if(a.type)
        {
          var ifs=this.checkCondition(a)

          if(tp=="and" && !ifs)
          {
            val=false
            //console.log()
          }
          if(tp=="or" && ifs)
          {
            val=true
            break
          } 

        }
        else
        { 
          var itm={} 
          if(a.questionnaireId==this.thisId)
          {
            var answ=this.getMyQue(a.questionId,a.valueId)
            var iskindof=this.isKindeOf(a.questionId,a.valueId)
           // console.log('/////////////////////////',answ)
            itm=this.myansw[a.questionId+""]

            var mval=a.valueId
            if(a.kindof!=null && typeof(a.kindof)!="undefined" && iskindof)
            {
              mval=a.kindof
            }

            if(typeof(mval)=="number")
              mval=a.questionId+"_"+mval
            var ifs=false  
            if(a.operation =="eq")
            {
              if(typeof(itm)=="undefined") return false; 
              ifs=(itm && ( (!a.value && itm[mval]) || (a.value && itm[mval]==a.value) )) 
            }
            if(a.operation =="lt")
            {
              if(typeof(itm)=="undefined") return false; 
              ifs=(itm && ( (!a.value && itm[mval]) || (a.value && itm[mval]<a.value) )) 
            }
            if(a.operation =="lte")
            {
              if(typeof(itm)=="undefined") return false; 
              ifs=(itm && ( (!a.value && itm[mval]) || (a.value && itm[mval]<=a.value) )) 
            }
            if(a.operation =="gt")
            {
              if(typeof(itm)=="undefined") return false; 
              ifs=(itm && ( (!a.value && itm[mval]) || (a.value && itm[mval]>a.value) )) 
            }
            if(a.operation =="gte")
            {
              if(typeof(itm)=="undefined") return false; 
              ifs=(itm && ( (!a.value && itm[mval]) || (a.value && itm[mval]>=a.value) )) 
            }
            if(a.operation =="ne")
            { 
              if(typeof(itm)=="undefined")
              {
                return true;
              }
              ifs=(itm && ( (!a.value && !itm[mval]) || (a.value && itm[mval]!=a.value) ))
              var ssss=0;
            }
            if(a.operation =="null")
            { 
              if(typeof(itm)=="undefined") return true; 
              ifs=(itm && ( itm[mval]==null || typeof(itm[mval])=='undefined'))
            }
            if(a.operation =="notnull")
            { 
              if(typeof(itm)=="undefined") 
                return false; 
                //a.questionId
                var existq=false
                if(itm)
                for(var ccc in itm)
                {
                  if(ccc.indexOf(a.questionId+"_")==0)
                    existq=true
                }
              //ifs=(itm && !( itm[mval]==null || typeof(itm[mval])=='undefined' ))
              ifs=existq
              var d=0;
            }
            if(tp=="and" && !ifs)
            {
              val=false
              //console.log()
            }
            if(tp=="or" && (itm && ifs))
            {
              val=true
              break
            } 
          }
          else
          {
            itm=this.gconds[a.questionnaireId][a.questionId]
          }
          
        }
      }
      return val;
