        this.questionAnsw[n]={}
        for(var jjx2 in a.answers)
        {
          var b=a.answers[jjx2]
          this.questionAnsw[n][a.id+'_'+b.id] ={id:a.id+'_'+b.id,q:a.id,a:b.id,text:b.text,expected:b.expected,url:b.url}
        }
        if(a.external)
        {
          //for(var i of this.qs.questions)
         // {
            for(var jjx3 in a.external)
            {
              var j=a.external[jjx3]
              var i = this.questionAnsw[j.id+""]
              //if(i.id==j.id)
              //{
                for(var xx in i)
                {
                  var x=i[xx]
                  var canp=false
                  if(j.type=="selected")
                  {
                    var qx=this.myansw[j.id+""]
                    if( qx && (qx[xx]!=null && typeof(qx[xx])!='undefined' && qx[xx]!=""))
                      canp=true
                  }
                  if(j.type=="notSelected"  && !x.isOther)
                  {
                    var qx=this.myansw[j.id+""]
                    if( qx && !(qx[xx]!=null && typeof(qx[xx])!='undefined' && qx[xx]!=""))
                      canp=true
                  }
                  if(j.type=="all")
                  {
                    if(x.id[0]=='o'  )
                    {
                      var qx=this.myansw[j.id+""]
                      if( qx && (qx[xx]!=null && typeof(qx[xx])!='undefined' && qx[xx]!=""))
                        canp=true
                    }
                    else
                      canp=true
                  }

                  if(canp)
                  {
                    if(x.isOther)//
                    {
                      var ss=i[x.id].q+""
                      this.questionAnsw[n][x.id]={id: x.id,q:ss,a:x.id,text:this.myansw[ss][x.id],expected:j.isStar}

                  //console.log('------->>>',j)
                    }
                    else
                    {
                    this.questionAnsw[n][x.id]={id: x.id,q:i[x.id].q,a:x.id,text:x.text,expected:j.isStar}

                  //console.log('------->>>',j)
                    }
                  }
                }
             // }
            }
          //}
        }
        if(a.other)
        {
          if(a.otherNum)
          {
            for(var x =0;x<a.otherNum;x++)
            {
              this.questionAnsw[n]['o'+a.id+'_'+x]={id:'o'+a.id+'_'+x,q:a.id,a:x,text:a.otherText,isOther:true}

            }

          }
        }
      }
