    getRandom(count)
    {
      var arr=[]
      for(var a=0;a<count;a++)
        arr.push(a);
      var newarr=[];
      for(var a=0;a<count;a++)
      {
        var rand=Math.floor(Math.random()*arr.length);
        var tmp=arr.splice(rand,1)
        newarr.push(tmp[0])
      }
      return newarr;
    },
    convertText(data){
      var start=data.indexOf("{{")
      var end=data.indexOf("}}")
      if(start>-1 && end>-1)
      {
        var txt=data.substr(start+2,end-start-2)
        var index=txt.replace("Ans_","")
        for(var a of this.qs.questions)
        {
          if(a.number==index)
          {
            var answ=this.myansw[a.id.toString()]
            var answindex=0;
            var answtitle="";
            for(var b in answ)
            {
              answindex= b.split("_")[1]
              answtitle=answ[b]
            } 
            if(a.kind=='radio' || a.kind=='check')
            {
              answtitle= a.answers.filter(p=>p.id==answindex)[0].text
            } 
            data=data.replace("{{"+txt+"}}",answtitle)
            console.log('|----->',answtitle)
            console.log('|----->',answ)
            console.log('|----->',a)
          }
        }
        console.log('|----->',txt)
        console.log('|----->',this.myansw)
        console.log('|----->',this.qs.questions)

        for(var a of this.qs.questions)
        {

        }

      }
      return data;
    },
    reload:function (){
      this.$forceUpdate()
      this.ques=getCopy( this.qs.questions[this.counter] )
      //console.log('-------------------------------------',this.getRandom(this.qs.questions[this.counter].answers.length))
      if(this.ques.kind=='endpage')
      {
        this.isEnd=true
      }
      this.ques.answers=[]
      var n=""+this.ques.id

      if(this.ques.rotation)
      {
        var myarr=[];
        var oth=[]
        for(var a in this.questionAnsw[n])
        {
          if(!this.questionAnsw[n][a].isOther)
            myarr.push(this.questionAnsw[n][a]);
          else
            oth.push(this.questionAnsw[n][a]);
        }
        
      //console.log('-------------------------------------',myarr)
        var myrandom=this.getRandom(myarr.length)
        for(var a of myrandom)
        {
          this.ques.answers.push(myarr[a])
        } 
        for(var a of oth)
        {
          this.ques.answers.push(a)
        } 

      }
      else
      {
        for(var a in this.questionAnsw[n])
        {
          this.ques.answers.push(this.questionAnsw[n][a])
        }

      }
      //console.log('-------------------------------------',myrandom)

      if(!this.myansw[this.ques.id])
      {
        this.myansw[this.ques.id]={}
        this.shErr[this.ques.id]={}
      }
      var self=this
      // setInterval(function(){
      //   self.ss++
      //   self.setState({})
      // },1000)
    },
        canNext:function (){
      this.reloadQuestion()
      this.shErr[this.ques.id]={}
      this.showRequire=false
      //console.log('--------------------->>---')
      //console.log(this.myansw[this.ques.id])
      //console.log(this.ques)
      if(this.ques.isStar)//isStar
      {
        var k= 0
        for(var a in this.myansw[this.ques.id])
        {
          if(this.myansw[this.ques.id][a])
            k++
        } 
        if(k==0)
        {
          this.showRequire=true
          this.chekText="این سوال اجباری است"
          return false 
        }

      }
      if(this.ques.expected )
      {
        // if(this.ques.kind=='radio')
        // {
        //   if(this.myansw[this.ques.id]>=0)
        //   {
        //      return true
        //   }
        //   this.showRequire=true
        //   this.chekText="سوال اجباری است"
        //   return false
        // }
        var k= 0
        for(var a in this.myansw[this.ques.id])
        {
          if(this.myansw[this.ques.id][a])
            k++
        } 
        //console.log('----->>---',k)
        if(this.ques.kind=='radio')
        {
          if(k==0)
          {
            this.showRequire=true
            this.chekText="این سوال اجباری است"
            return false 
          }
        }
        else if(this.ques.min)
        {
          if(k<this.ques.min)
          {
            this.showRequire=true
            this.chekText="حد اقل باید "+ this.ques.min +" مورد انتخاب شود"
            return false

          }
        }
        else
        {
          if(k<1)
          {
            this.showRequire=true
            this.chekText="حد اقل باید "+ 1 +" مورد انتخاب شود"
            return false

          }

        }
        if(this.ques.max)
        {
          if(k>this.ques.max)
          {
            this.showRequire=true
            this.chekText="حد اکثر باید "+ this.ques.max +" مورد انتخاب شود"
            return false

          }

        }

      }
      for(var jjx4 in this.ques.answers)
      {
        var a=this.ques.answers[jjx4]
        // console.log('---------------------------------')
        // console.log(a)
        // console.log(this.myansw[this.ques.id][a.id])
        // console.log(this.myansw[this.ques.id])
        if(a.expected && !this.myansw[this.ques.id][a.id])
        {
          this.shErr[this.ques.id][a.id]=true
          // console.log(this.shErr)
          this.cc++
          return false
        }
        
      }
      return true
    },
