    submit:function (){

      var t= getCookie('token')
      var anss=[]

      // console.log('ssssssssssssssssssssssss',this.myansw)
      for(var a in this.myansw)
      {
        var numk=Object.keys(this.myansw[a])
        if(numk.length==0)
          continue
        var mq=this.getQuesById(a)
        var ao={
          id:a,
          text:mq.text,
          number:mq.number,
          kind:mq.kind,
          answer:[]
        }
        for(var b in this.myansw[a])
        {
          var answ=this.myansw[a][b]
          if(answ===true)
          {
            answ=this.questionAnsw[a+""] [b].text
          }
          var adansw={
            id:b,
            text : this.questionAnsw[a+""] [b].text,
            answer:answ
          }
          if(mq.kind=="radio")
          {
            if(b[0]!="o")
              adansw.answer=this.questionAnsw[a+""] [b].text
          }
          ao.answer.push(adansw)
          //if(mq.kind)
          
        }
        anss.push(ao)
      }
      var objx={
        questionnaireId:this.thisId,
        "startDate": this.startDate,
        "endDate": new Date(),
        answer:anss
      }
      //console.log('--------------->',objx)
      var strurl=global1.apiPath+'/v1/a';
      if(this.isPublic)
      {
        strurl+='/public';
      }
      var q=httpPostData(strurl,objx,{
        'x-auth-token':t
      }) 
      //console.log('---q------------>',q)
      if(q.trackingCode)
      {
        if(this.qs.isVoice)
        {
        stopRecording()

        }
        this.ques.text="اطلاعات ارسال شد" +" کد پیگیری شما  به صورت زیر می باشد"
        this.ques.description=""
        this.trackingCode =q.trackingCode
        this.issend=true

      }
      else
      {
        this.ques.text="کد پیگیری دریافت نشد دوباره ارسال کنید"
      }
    },
        
