mounted:function (){ 
      var id=getParams(this,"id") 
      this.thisId=id
      var t= getCookie('token')
      var q=httpGetData(global1.apiPath+'/v1/q/'+id,{},{
        'x-auth-token':t
      }) 
      var questcounts=0;
      for(var a of q.questions)
      {

        if(a.kind=="phone")
        {
          questcounts++;
          q.conditions.push(
            {
              id:"phonecond"+questcounts,
            goTo:2,
            subCond:{
              type:"and",
              children:[
                {
                  id:"phonecondx"+questcounts,
                  operation: "ne",
                  questionId:a.id,
                  questionnaireId:id,
                  value:"",
                  valueId:99999991
                  },
                  {
                  id:"phonecondy"+questcounts,
                  operation: "notnull",
                  questionId:a.id,
                  questionnaireId:id,
                  value:"",
                  valueId:99999991
                  }
              ]
            }
            }
          );
          a.kind=["radio"]
          a.answers=[
            {id:99999991,text:"موفق"},
            {id:99999992,text:"عدم همکاری"},
            {id:99999993,text:"خط به شخص دیگری واگذار شده"},
            {id:99999994,text:"عدم اطلاعات کافی از طرح"},
            {id:99999995,text:"اشغال"},
            {id:99999996,text:"خارج از شبکه"},
            {id:99999997,text:"خاموش"},
            {id:99999998,text:"در حال حاضر مشغول هستم بعدا تماس بگیرید"},
            {id:99999999,text:"صاحب خط در حال حاضر حضور ندارد"},
          ]
        }
      }
      //console.log('data is --->',q)
      this.isPublic= q.isPublic;
      var w=q.welcomePage
      var wobj={ 
        answers:[{text:w.buttonTitle}]
        ,kind:"page"
      }
      var eob=q.endPage
      var eobj={ 
        answers:[{text:eob.buttonTitle}]
        ,kind:"endpage"
      }

      if(eob.viewUrl)
        eobj.url=eob.media
        
      if(eob.viewTitle)
        eobj.text=eob.title
        
      if(eob.description)
        eobj.description=eob.description

      q.questions.push( eobj)       

      if(w.viewUrl)
        wobj.url=w.media

      if(w.viewButtonTitle)
        wobj.buttonTitle=w.buttonTitle
        
      if(w.viewTitle)
        wobj.text=w.title
        
      if(w.description)
        wobj.description=w.description

      q.questions.splice(0,0,wobj)       
      for(var jjx12 in q.questions)
      {
        var a= q.questions[jjx12] 
        this.myansw[a.id+""]={}
        this.shErr[a.id+""]={}
        for(var jjx13 in a.answers)
        {
          var b=a.answers[jjx13]
          b.mclass='form-control'
          if(b.expected)
            b.mclass+=' required'

        }
      }
      this.ques=q.questions[this.counter]
      this.qs=q
      this.getAllCond(q.conditions)
      
      this.reloadQuestion()
      this.reload()
     // console.log('getParams',dts)
    },

     
   
}
</script>